html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: #030217; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.container {
  color: #1a1399;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.dashboard {
  border: 2px solid #070538;
  margin: 0;
  height: 90vh;
  width: 80%;
  border-radius: 20px;
  overflow: hidden; }

.search-bar, .menu {
  background: #04031D; }

.search-bar {
  width: 100%;
  height: 15%;
  border-bottom: 1.5px solid #0c094f;
  align-content: center;
  justify-content: center;
  align-items: center;
  display: flex; }

.form {
  width: 30%;
  position: relative;
  text-align: center;
  margin-right: 60px; }

.form input {
  all: none;
  background: #070534;
  border: none;
  border-radius: 30px;
  padding: 10px 35px 10px 15px;
  color: #2219c6;
  width: 90%; }

.form input::-webkit-input-placeholder {
  color: #2219c6; }

.form input::-ms-input-placeholder {
  color: #2219c6; }

.form input::placeholder {
  color: #2219c6; }

.search-icon {
  position: absolute;
  top: 7px;
  right: -5px; }

.menu {
  width: 20%;
  height: 85%;
  float: left;
  overflow-y: auto; }

.menu ul {
  list-style-type: none; }

.menu ul li {
  margin-left: 0px;
  padding-top: 10px;
  padding-bottom: 10px;
  -webkit-transition: margin-left .2s;
  transition: margin-left .2s; }

.menu ul li:hover {
  cursor: pointer;
  margin-left: 10px; }

.dashboard-content {
  background: #1C194C;
  width: 80%;
  height: 85%;
  float: right;
  overflow-y: auto; }

.dashboard-content h1 {
  text-align: center; }

.movie-container {
  margin: 10px auto;
  width: 95%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap; }

.dashboard-content-movie {
  flex: 0 1 auto;
  width: 20%;
  margin: 20px;
  text-align: center;
  border-radius: 10px;
  overflow: hidden;
  height: auto;
  background: #070534;
  -webkit-animation: fadeIn 2.5s;
          animation: fadeIn 2.5s; }

@-webkit-keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.dashboard-content-movie img {
  width: 100%; }

.pagination {
  width: 30%;
  list-style: none;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 30px auto;
  padding: 0;
  border-radius: 20px;
  overflow: hidden;
  background: #0a084b; }

.itemClass {
  padding: 12px; }

.activeClass {
  background: #070534; }

.linkClass {
  color: #878D9F;
  text-decoration: none; }

::-webkit-scrollbar {
  width: 10px; }

::-webkit-scrollbar-track {
  background: #f1f1f1; }

::-webkit-scrollbar-thumb {
  background: #888; }

::-webkit-scrollbar-thumb:hover {
  background: #555; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

